.wrapper {
  overflow: hidden;
}
.display2 {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  gap: 30px;
  .title-figures {
    display: flex;
    align-items: center;
    gap: 7px;
    span:nth-child(1) {
      display: block;
      width: 66px;
      height: 9px;
      background: #82bde5;
      border-radius: 3px;
    }
    span:nth-child(2),
    span:nth-child(3),
    span:nth-child(4) {
      width: 9px;
      height: 9px;
      background: #82bde5;
      border-radius: 50%;
    }
  }
  .title-figures__left {
    span:nth-child(1) {
      order: 2;
    }
  }
}
h2 {
  position: relative;
  font-size: 55px;
  line-height: 120%;
  font-weight: 600;
  color: #2d709d;
}
.btn {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.btn-blue {
  background: #ff5700;
  background-image: linear-gradient(90.0deg, #38509b -24%, #0084ff 125%);
  border-radius: 25px;
  &:hover {
    background-image: linear-gradient(90.0deg, #0084ff -24%, #38509b 125%);
  }
  span {
    line-height: 130%;
    font-weight: 700;
    letter-spacing: 0.1em;
    color: #ffffff;
  }
}
.btn-orange {
  background-color: #ff5700;
  background-image: linear-gradient(90.0deg, #cc1300 -24%, #ff8a00 125%);
  border-radius: 25px;
  &:hover {
    background-image: linear-gradient(90.0deg, #ff8a00 -24%, #cc1300 125%);
  }
  span {
    line-height: 130%;
    font-weight: 700;
    letter-spacing: 0.1em;
    color: #ffffff;
  }
}
.swiper-button-next,
.swiper-button-prev {
  z-index: 1;
  width: 60px;
  height: 60px;
  background: transparent;
  border: 1px solid #c2c2c2;
  border-radius: 50%;
  margin-top: 0;
  &:after {
    font-size: 28px;
    color: #82bce5;
  }
}
.swiper-pagination {
  &-bullet {
    width: 15px;
    height: 15px;
    background: transparent;
    border: 1px solid #c2c2c2;
    opacity: 1;
  }
  &-bullet-active {
    background: #82bce5;
    border-color: transparent !important;
  }
}
.services-card {
  position: relative;
  background-repeat: no-repeat;
  background-position: center;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  background-size: cover;
  border-radius: 25px;
  padding: 30px;
  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    background-image: linear-gradient(180.0deg, rgba(0,0,0,0.0) -9%, rgba(0,0,0,0.64) 110%);
    border-radius: 25px;
    pointer-events: none;
  }
  &__link {
    position: relative;
    z-index: 1;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    span {
      font-size: 22px;
      line-height: 130%;
      font-weight: 500;
      color: #ffffff;
    }
    i {
      flex-shrink: 0;
      width: 34px;
      margin-left: 15px;
      img {
        width: 100%;
        height: auto;
      }
    }
  }
  &:hover {
    &:after {
      background-image: linear-gradient(90.0deg, rgba(56,80,155,0.69) -24%, rgba(0,132,255,0.69) 125%);
    }
  }
}
.up {
  position: fixed;
  z-index: 5;
  bottom: 30px;
  right: 30px;
  width: 60px;
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #71baeb;
  border-radius: 50%;
  opacity: 0;
  pointer-events: none;
  &--active {
    cursor: pointer;
    opacity: 1;
    pointer-events: all;
  }
}

@media screen and (max-width: 1200px) {
  .display2 {
    .title-figures {
      gap: 5px;
      span:nth-child(1) {
        width: 50px;
        height: 6px;
      }
      span:nth-child(2),
      span:nth-child(3),
      span:nth-child(4) {
        width: 6px;
        height: 6px;
      }
    }
  }
  h2 {
    font-size: 45px;
  }
  .swiper-button-next,
  .swiper-button-prev {
    width: 45px;
    height: 45px;
    &:after {
      font-size: 20px;
    }
  }
  .services-card {
    border-radius: 16px;
    padding: 20px;
    &:after {
      border-radius: 16px;
    }
    &__link {
      span {
        font-size: 17px;
      }
      i {
        width: 26px;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  h2 {
    font-size: 32px;
  }
  .services-card {
    padding: 20px 15px;
    &__link {
      span {
        font-size: 14px;
      }
      i {
        width: 21px;
      }
    }
  }
  .swiper-pagination {
    &-bullet {
      width: 10px;
      height: 10px;
    }
  }
  .up {
    right: 20px;
  }
}

@media screen and (max-width: 740px) {
  .display2 {
    gap: 16px;
    .title-figures {
      gap: 3px;
      span:nth-child(1) {
        width: 28px;
        height: 4px;
      }
      span:nth-child(2),
      span:nth-child(3),
      span:nth-child(4) {
        width: 4px;
        height: 4px;
      }
    }
  }
  h2 {
    font-size: 24px;
  }
  .swiper-button-prev,
  .swiper-button-next {
    width: 38px;
    height: 38px;
    &:after {
      font-size: 16px;
    }
  }
}

@media screen and (max-width: 576px) {
  .services-card {
    &__link {
      br {
        display: none;
      }
    }
  }
}

