.footer {
  color: #ffffff;
  background: #0d4c99;
  padding: 50px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
  }
  &__left {
    .footer__brevis {
      display: none;
    }
  }
  &__right {
    flex-grow: 1;
    display: flex;
    justify-content: space-between;
    gap: 30px;
    margin-left: 100px;
  }
  &__logo {
    width: 375px;
    display: block;
    img {
      width: 100%;
    }
  }
  &__btn {
    width: 321px;
    height: 52px;
    background: transparent;
    border: 2px solid #ffffff;
    border-radius: 25px;
    margin-top: 20px;
    &:hover {
      background: #ffffff;
      span {
        color: #000000;
      }
    }
    span {
      font-size: 16px;
      line-height: 120%;
      font-weight: 800;
      text-transform: uppercase;
      letter-spacing: 0.1em;
      color: #ffffff;
    }
  }
  &__text {
    font-size: 18px;
    line-height: 130%;
    margin-top: 100px;
    strong {
      font-weight: 700;
    }
  }
  &__subtitle {
    font-size: 26px;
    line-height: 130%;
    font-weight: 700;
  }
  ul {
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
    margin-top: 20px;
    li {
      font-size: 18px;
      line-height: 130%;
      font-weight: 500;
    }
    li a:hover {
      opacity: 0.7;
    }
  }
  &__brevis {
    display: flex;
    margin-top: 60px;
    &-icon {
      flex-shrink: 0;
      width: 42px;
      margin-right: 15px;
      img {
        width: 100%;
      }
    }
    &-text {
      font-size: 16px;
      line-height: 130%;
      color: #9bb9de;
      a {
        font-weight: 700;
        text-decoration: underline;
        text-underline-offset: 3px;
      }
    }
  }
}

@media screen and (max-width: 1500px) {
  .footer {
    &__right {
      margin-left: 60px;
    }
    &__logo {
      width: 280px;
    }
    &__btn {
      width: 280px;
    }
  }
}

@media screen and (max-width: 1200px) {
  .footer {
    padding: 40px 0;
    &__btn {
      width: 241px;
      height: 40px;
      span {
        font-size: 14px;
      }
    }
    &__text {
      font-size: 14px;
      margin-top: 70px;
    }
    &__subtitle {
      font-size: 20px;
    }
    ul {
      li {
        font-size: 14px;
      }
    }
    &__brevis {
      margin-top: 40px;
      &-icon {
        width: 32px;
        margin-right: 12px;
      }
      &-text {
        font-size: 14px;
      }
    }
  }
}

@media screen and (max-width: 1160px) {
  .footer {
    &__logo {
      width: 230px;
    }
  }
}

@media screen and (max-width: 992px) {
  .footer {
    padding: 30px 0;
    &__subtitle {
      font-size: 16px;
    }
  }
}

@media screen and (max-width: 960px) {
  .footer {
    &__left {
      .footer__brevis {
        display: flex;
        margin-top: 20px;
      }
    }
    &__right {
      flex-grow: unset;
    }
    &__text {
      margin-top: 20px;
    }
    &__group:nth-child(2) ~ .footer__group {
      display: none;
    }
  }
}

@media screen and (max-width: 740px) {
  .footer {
    &__inner {
      display: block;
    }
    &__left {
      display: flex;
      flex-direction: column;
      align-items: center;
      text-align: center;
    }
    &__right {
      display: none;
    }
    &__logo {
      width: 200px;
    }
    &__brevis {
      text-align: start;
    }
  }
}